.btn-group {
    min-width: 80px;
}

.link-button {
    background-color: transparent;
    border: none;
    cursor: pointer;
    text-decoration: underline;
    display: inline;
    margin: 0;
    padding: 0;
}

.link-button:hover,
.link-button:focus {
    text-decoration: none;
}

.description, .error-message {
    font-size: .95rem;
    font-weight: 400;
}

.disabled {
    cursor: not-allowed;
}

.popper {
    z-index: 900;
}

.search-icon {
    fill: currentcolor;
}

.manual-entry-link {
    margin-top: -1.8rem;
}

.btn {
    padding-top: .3rem;
    padding-bottom: .3rem;
}